import React from 'react';
import './AttachedDevicesComponent.css';
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import List from "@material-ui/core/List";
import Cookies from "js-cookie";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import {AccountVerificationContext} from "../../contexts/Contexts";
import Divider from "@material-ui/core/Divider";
import Avatar from "@material-ui/core/Avatar";
import ListSubheader from "@material-ui/core/ListSubheader";

const AppData = {
    account: Cookies.get('default_account') ? JSON.parse(Cookies.get('default_account')) : null,
};
const AttachedDevicesComponent = () => {
    const [account, setAccount] = React.useContext(AccountVerificationContext);
    return (
        <div className="AttachedDevicesComponent mb-3">
            <Container className={"mt-5 mb-4"} maxWidth={"xs"}>
                <Typography variant={"h3"}>
                    Devices
                </Typography>
                <Typography variant={"caption"}>
                    View your Verified Devices Here.
                    Your Kabeers Network Account Profile
                </Typography>
            </Container>
            <Container maxWidth={"xs"}>
                <List className={"mt-5 text-left"} subheader={<ListSubheader>Settings</ListSubheader>}>
                    <div style={{display: "inline-flex", justifyContent: "center"}} className={"w-100"}>
                        <Avatar alt={account.username}
                                src={account.account_image}/>
                    </div>
                    <div className={"text-center"}>
                        <ListItemText id="switch-list-label-wifi"
                                      primary={`Welcome ${account.username}`}/>
                    </div>
                    <Divider/>
                    {
                        account.webauthn_devices.map(id => <ListItem button><ListItemText primary={id}/></ListItem>)
                    }
                </List>
                AttachedDevicesComponent Component
            </Container>
        </div>
    );
};
AttachedDevicesComponent.propTypes = {};

AttachedDevicesComponent.defaultProps = {};

export default AttachedDevicesComponent;
